.account-page {
  a {
    color: #444;
  }
  .intro {
    border-bottom: 1px solid #ddd;
  }
  .icon {
    color: #888;
  }
  .name {
    color: #444;
  }
  &-menu {
    &-item {
      &.active,
      &:hover,
      &:active {
        & > * {
          color: #3a5dd9;
          font-weight: bold;
        }
      }
    }
  }
}

@media only screen and (min-width: 768px) {
    .account-page {
      min-height: 100vh;
    }
  }

